        var end = importer.center;

        var mid = start.clone().lerp( end, 0.5 );
        var midLength = mid.length();
        mid.normalize();
        mid.multiplyScalar( midLength + distanceBetweenCountryCenter * 0.7 );

        var normal = ( new THREE.Vector3() ).subVectors( start, end );
