        var splineCurveB = new THREE.CubicBezierCurve3( mid, midEndAnchor, endAnchor, end );

        var vertexCountDesired = Math.floor( distanceBetweenCountryCenter * 0.02 + 6 ) * 2;

        var points = splineCurveA.getPoints( vertexCountDesired );

